import React, { Component } from 'react';
import './App.css';
import logo from './img/logo.png';
//import TronWeb from 'tronweb';
import { InputGroup, 
  InputGroupAddon, 
  Input, 
  Button, 
  InputGroupText, 
  Modal, 
  ModalHeader, 
  ModalBody,
  ModalFooter, 
  UncontrolledTooltip, 
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Card, 
  CardImg, 
  CardText, 
  CardBody,
  CardTitle,
  Spinner,
 } from 'reactstrap';
import {units, faq, gameRules, startInfo, referralInfo, footerLinks, sponsorWarning} from './config';

const SUN_IN_TRX = 1000000;
const ENERGY_LIMIT = 10000000;
const ZERO_ADDRESS = "410000000000000000000000000000000000000000"
const NUMBER_OF_UNITS = 6;
const PERIOD = 6;
const CONTRACT_ADDRESS = "TJYNMUtUeDTEQGHKQQ7ZijMkh1E2kGKg4C";
const LOADING = -1;

let tronWeb = false;
let sponsor = ZERO_ADDRESS;
let refLink = window.location.search.slice(3)

if(refLink.length > 30 && refLink.length < 36) {
  sponsor = escape(refLink);
  document.cookie = `ref=${sponsor};max-age=31536000`;
}
else {
  let refCookie = document.cookie.split(';').filter((item) => item.trim().startsWith('ref='));
  if (refCookie.length > 0) {
    sponsor = refCookie[0].slice(4);
  }
}


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      refModal: false,
      rulesModal: false,
      faqModal: false,
      sponsorValid : false,
      sponsorInalid : false,
      showSponsorWarning: false,
      playerRefList : [],
      navbarOpen : false,
      address : "Welcome to Tron Worlds!",
      gameInfo : { balance : 0, payouts : 0, players : 0, units : 0, doomsday : 0 },
      playerInfo : {withdrawable : 0, repurchase : 0, refEarnings : 0, 
        noFirstUnit : true, time : LOADING, units : new Array(NUMBER_OF_UNITS).fill(0),
        },
    };
    this.withdraw = this.withdraw.bind(this);
    this.deposit = this.deposit.bind(this);
    this.buy = this.buy.bind(this);
    this.checkSponsor = this.checkSponsor.bind(this);
    this.toggleRefModal = this.toggleRefModal.bind(this);
    this.toggleRulesModal = this.toggleRulesModal.bind(this);
    this.toggleFAQModal = this.toggleFAQModal.bind(this);
    this.navbarToggle = this.navbarToggle.bind(this);
    this.sponsor = ZERO_ADDRESS;
    this.units = units;
  }

  async getGameInfo() {
    let game =  await this.contract.getGameInfo().call();
    this.setState({gameInfo: { balance : (game[0].toNumber() / SUN_IN_TRX), 
        payouts : (game[3].toNumber() / SUN_IN_TRX), players : game[1].toNumber(),
        units : game[2].toNumber(), doomsday : game[4].toNumber() }});
  }

  async getPlayerInfo() {
    let player =  await this.contract.getPlayerInfo(this.account.address).call();
    let  playerUnits = new Array(NUMBER_OF_UNITS).fill(0);
    let withdrawable = 0;
    let repurchase = 0;
    let playerTime = player[2].toNumber();
    if (playerTime > this.state.gameInfo.doomsday - PERIOD) {
        playerUnits = player[5];     
        withdrawable = player[0].toNumber() / SUN_IN_TRX;
        repurchase = player[1].toNumber() / SUN_IN_TRX;
        let periods = Math.floor((this.time_now - playerTime) / PERIOD);
        if (periods > 0) {
          let profitPerPeriod = 0;
          for (let i = 0; i < NUMBER_OF_UNITS; i++) {
            profitPerPeriod += (this.units[i].profit * playerUnits[i]);
          }
          let profit = periods * profitPerPeriod;
          repurchase += (profit / 2);
          withdrawable += (profit / 2);
        }
    }
    this.setState({playerInfo: {withdrawable : withdrawable.toFixed(2), repurchase : repurchase.toFixed(2), 
        refEarnings : player[3].toNumber() / SUN_IN_TRX, 
        noFirstUnit : player[4] === ZERO_ADDRESS, time : playerTime, units : playerUnits,
        }});

    this.setState({loading: false});
  }

  async getInfo() {
    let block = await tronWeb.trx.getCurrentBlock();
    this.time_now = block.block_header.raw_data.timestamp / 1000;
    await this.getGameInfo();
    this.getPlayerInfo();
  }

  async checkSponsor(sponsor_) {
    if (tronWeb.isAddress(sponsor_) && await this.contract.checkSponsor(sponsor_).call()) {
      this.setState({sponsorValid: true, sponsorInvalid: false});
      this.sponsor = sponsor_;
    } else {
      this.setState({sponsorValid: false, sponsorInvalid: true});
    }
  }

  async toggleRefModal() {
    if (this.state.refModal) this.setState({refModal: false});
    else {
      this.setState({refModal: true});
      if (this.contract) {
        let refList = await this.contract.getPlayerRefferalsList().call();
        this.setState({playerRefList: refList.map(item => tronWeb.address.fromHex(item))});
      }
    }
  }

  toggleRulesModal() {
    this.setState({
      rulesModal: !this.state.rulesModal
    });
  }

  toggleFAQModal() {
    this.setState({
      faqModal: !this.state.faqModal
    });
  }

  navbarToggle() {
    this.setState({
      navbarOpen: !this.state.navbarOpen
    });
  }

  async deposit(amount) {
    this.setState({loading: true});
    let args = {
      feeLimit:ENERGY_LIMIT,
      callValue:amount * SUN_IN_TRX,
      shouldPollResponse: true
    };
    try {
      if (this.state.playerInfo.time !== 0) await this.contract.deposit().send(args);
      else await this.contract.firstDeposit(this.sponsor).send(args);
    } catch(e) {};
    this.getInfo();
  }

  async withdraw(amount) {
    this.setState({loading: true});
    let args = {
      feeLimit: ENERGY_LIMIT,
      callValue: 0,
      shouldPollResponse: true
    };
    try {
      await this.contract.withdraw(amount * SUN_IN_TRX).send(args);
    } catch(e) {};
    this.getInfo();
  }

  async buy(type, amount) {
    this.setState({loading: true});
    let args = {
      feeLimit: ENERGY_LIMIT,
      callValue: 0,
      shouldPollResponse: true
    };
    try {
      await this.contract.buyUnit(type, amount).send(args);
    } catch(e) {};
    this.getInfo();
  }

  async componentDidMount() {
    if (window.tronWeb) tronWeb = window.tronWeb;
    if (tronWeb) {
      this.contract = await tronWeb.contract().at(CONTRACT_ADDRESS);
      if (tronWeb.isAddress(sponsor) && await this.contract.checkSponsor(sponsor).call()) {
          this.sponsor = sponsor;
          this.setState({showSponsorWarning: false});
      } else {
        this.setState({showSponsorWarning: true});
      }
      this.account = await tronWeb.trx.getAccount();    
      this.setState({address: tronWeb.address.fromHex(this.account.address)});
      this.getInfo();
    }
  }

  render() {
    if (this.state.gameInfo.doomsday - this.time_now > 0) return (
      <Doomsday time = {this.state.gameInfo.doomsday - this.time_now} />
    );
    let refBox = this.state.playerInfo.time === 0 && this.state.showSponsorWarning;
    return (
      <div className="App">
        <Navbar dark fixed="top" expand="lg">
        <div className="container">
          <NavbarBrand href="/"><img src={logo} height="40px" width="40px"/>Tron Worlds</NavbarBrand>
          <NavbarToggler onClick={this.navbarToggle} />
          <Collapse isOpen={this.state.navbarOpen} navbar>
          <Nav className="mr-auto" navbar>
              <NavItem>
                <Button color="success" onClick = {this.toggleRulesModal}>
                Tutorial
                </Button>
              </NavItem>
            </Nav>
            <h4 className="text-center d-none d-lg-block">{this.state.address}</h4>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <Button color="success" onClick = {this.toggleFAQModal}>
                  FAQ
                </Button>
              </NavItem>
              <NavItem>
                <Button color="info" onClick = {this.toggleRefModal}>
                  Referral Info
                </Button>
              </NavItem>
            </Nav>
          </Collapse>
          </div>
        </Navbar>
        <div className="content  container">
        <div className="row">
          <div className="col-md-6">
            <GameInfo gameInfo = {this.state.gameInfo}/>
          </div>
          <div className="col-md-6">
            {this.state.playerInfo.time === LOADING ? <DepositBoxLoading /> : <DepositBox playerInfo = {this.state.playerInfo} deposit = {this.deposit} withdraw = {this.withdraw}/>}
          </div>
        </div>
        <Units units = {units} playerUnits = {this.state.playerInfo.units} noFirstUnit = {this.state.playerInfo.noFirstUnit} buy = {this.buy} time = {this.state.playerInfo.time}/>
        <div className="BottomInfo">
        <p>TronWorlds is an idle economic strategy game under security of Tron blockchain.</p>
        </div>
        </div>
        <footer>
        <div className="container">
        <Nav pills justified>
         {footerLinks.map((item, key) => <NavItem key={key}><NavLink href={item.url} target="_blank">{item.name}</NavLink></NavItem>)}
        </Nav>
        </div>
        </footer>
        <ReferralInfo refList={this.state.playerRefList} playerInfo={this.state.playerInfo} address={this.state.address} modal={this.state.refModal} toggleFun={this.toggleRefModal}/>
        <GamesRules modal={this.state.rulesModal} toggleFun={this.toggleRulesModal}/>
        <FAQ modal={this.state.faqModal} toggleFun={this.toggleFAQModal}/>
        {refBox && <RefBox checkSponsor={this.checkSponsor} sponsorValid= {this.state.sponsorValid} sponsorInvalid= {this.state.sponsorInvalid}/>}
        {this.state.loading && <div className="spin"><Spinner style={{ width: '4rem', height: '4rem' }} color="success" /></div>}
      </div>
    );
  }
}

function DepositBoxLoading(props) {
    return (
      <Card className="DepositBox">
        <CardBody>
          <CardTitle><h4>Player Info</h4></CardTitle>
          <CardText dangerouslySetInnerHTML={{__html: startInfo}} />
        </CardBody>
      </Card>
    );
  }

class DepositBox extends Component {

  constructor(props) {
    super(props);
    this.state = { balance : 0 };
    this.withdraw = React.createRef();
    this.deposit = React.createRef();
    this.onWithdraw = this.onWithdraw.bind(this);
    this.onDeposit = this.onDeposit.bind(this);
    this.setAmount = this.setAmount.bind(this);
  }

  async getBalance() {
    let balance = await tronWeb.trx.getBalance();
    let new_state = Object.assign({}, this.state);
    new_state.balance = (balance / SUN_IN_TRX).toFixed(2);
    this.setState(new_state);
  }

  componentDidMount() {
    this.interval = setInterval(() => this.getBalance(), 4000);
    this.getBalance();
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  onWithdraw() {
    let amount = Number(this.withdraw.current.value);
    this.props.withdraw(amount);
  }

  onDeposit() {
    let amount = Number(this.deposit.current.value);
    this.props.deposit(amount);
  }

  setAmount() {
    this.withdraw.current.value = this.props.playerInfo.withdrawable;
  }

  render() {
    return (
      <Card className="DepositBox">
        <CardBody>
          <CardTitle><h4>Player Info</h4></CardTitle>
          <CardText>
            <span id="withdrawable" onClick={this.setAmount}>Withdrawable: <b>{this.props.playerInfo.withdrawable}</b> TRX</span><br />
            <span id="repurchase">Repurchase: <b>{this.props.playerInfo.repurchase}</b> TRX</span>
          </CardText>
          <UncontrolledTooltip placement="top" target="withdrawable">
            Half of your earning goes on your withdrawable balance. You can withdraw it or buy another units.
          </UncontrolledTooltip>
          <UncontrolledTooltip placement="top" target="repurchase">
          Half of your earning goes on your repurchase balance. You can buy another units with it.
          </UncontrolledTooltip>
          <UncontrolledTooltip placement="top" target="deposit">
          Deposited funds go to your repurchase balance. Don't deposit everything you have, you need to pay transaction fees about 1 TRX when buy units or withdraw funds.
          </UncontrolledTooltip>
        <div className="row">
        <InputGroup className="col-lg-6" id="deposit">
          <InputGroupAddon addonType="prepend">TRX</InputGroupAddon>
          <Input type="text" innerRef={this.deposit} defaultValue = "0"/>
          <InputGroupAddon addonType="append">
            <Button color="danger" onClick = {this.onDeposit}>
              Deposit
            </Button>
          </InputGroupAddon>
        </InputGroup>
        <InputGroup className="col-lg-6">
          <InputGroupAddon addonType="prepend">TRX</InputGroupAddon>
          <Input type="text" innerRef={this.withdraw} defaultValue = "0"/>
          <InputGroupAddon addonType="append">
            <Button color="success" onClick = {this.onWithdraw}>
              Withdraw
            </Button>
          </InputGroupAddon>
        </InputGroup>
        </div>
        <CardText className="text-left"><small className="text-muted">Your wallet balance: <b>{this.state.balance}</b> TRX</small></CardText>
        </CardBody>
      </Card>
    );
  }
}

function GameInfo(props) {
  let startDate = new Date(props.gameInfo.doomsday * 1000);
  return (
      <Card className="GameInfo">   
        <CardBody>
          <CardTitle><h4>Game Info</h4></CardTitle>
          <CardText>
            Total Buys: <b>{(props.gameInfo.balance + props.gameInfo.payouts).toLocaleString('en')} TRX</b><br />
            Total payouts: <b>{props.gameInfo.payouts.toLocaleString('en')} TRX</b><br />
            Registered players: <b>{props.gameInfo.players}</b><br />
            Active units: <b>{props.gameInfo.units}</b><br />
            Started: <b>{startDate.toLocaleDateString()}</b>
          </CardText>
        </CardBody>
      </Card>
  );
}

function ReferralInfo(props) {
  return (
    <Modal isOpen={props.modal} toggle={props.toggleFun}>
    <ModalHeader toggle={props.toggleFun}>Account Info</ModalHeader>
    <ModalBody>
      <p dangerouslySetInnerHTML={{__html: referralInfo}} />
      {props.playerInfo.time !== LOADING && <p>Your ref link:<br /> <span className="text-primary">https://tronworlds.io/?r={props.address}</span></p>}
      <p>Your referral earnings: {props.playerInfo.refEarnings} TRX</p>
      <p>Your referrals:</p>
      {props.refList.map((item, key) => <p key={key}>{item}</p>)}
    </ModalBody>
    <ModalFooter>
      <Button color="secondary" onClick={props.toggleFun}>Close</Button>
    </ModalFooter>
    </Modal>
  );
}

function GamesRules(props) {
  return (
    <Modal isOpen={props.modal} toggle={props.toggleFun}>
    <ModalHeader toggle={props.toggleFun}>Tutorial</ModalHeader>
    <ModalBody>
      <p dangerouslySetInnerHTML={{__html: gameRules}} />
    </ModalBody>
    <ModalFooter>
      <Button color="secondary" onClick={props.toggleFun}>Close</Button>
    </ModalFooter>
    </Modal>
  );
}

function FAQ(props) {
  return (
    <Modal isOpen={props.modal} toggle={props.toggleFun}>
    <ModalHeader toggle={props.toggleFun}>FAQ</ModalHeader>
    <ModalBody>
      {faq.map((item, key) => <div key={key}><p className="question"><b>Q:</b> {item.q}</p><p className="answer"><b>A:</b> <span dangerouslySetInnerHTML={{__html: item.a}} /></p></div>)}
    </ModalBody>
    <ModalFooter>
      <Button color="secondary" onClick={props.toggleFun}>Close</Button>
    </ModalFooter>
    </Modal>
  );
}

function Units(props) {
  
  return(
    <div className="Units">
    <h2>Units</h2>
    <p>Buy these units and get paid TRX every hour.</p>
    <div className="row">
      {props.units.map((item, key) => <Unit key = {key} id = {key} price = {item.price} profit = {item.profit} name = {item.name} pic = {item.pic} description = {item.description} disabled = {(props.time === LOADING) || (props.noFirstUnit && key === 0)} quantity = {props.playerUnits[key]} buy = {props.buy}/>)}
    </div>
    </div>
  );
}

class Unit extends Component  {

  constructor(props) {
    super(props);
    this.buy = React.createRef();
    this.onBuy = this.onBuy.bind(this);
  }

  onBuy() {
    let amount = Number(this.buy.current.value);
    this.props.buy(this.props.id, amount);
  }
  render() {
    let disabled = this.props.disabled
    let buyButton = "buy-button" + this.props.id;
    return(
      <div className="col-md-6 col-xl-4">
      <Card className="Unit">
        <CardBody>
          <CardImg top width="100%" src={this.props.pic} alt={this.props.name}/>
          <CardTitle className="text-danger"><b>{this.props.name}</b></CardTitle>
          <CardText className="text-warning">{this.props.description}</CardText>
          <CardText>
            Profit per hour: <b>{this.props.profit}</b> TRX<br />
            Profit per month: <b>{((this.props.profit * 72000) / this.props.price).toFixed()}</b> %<br />
            Price: <b>{this.props.price}</b> TRX<br />
            You own: <b>{this.props.quantity}</b>
          </CardText>
          {!disabled && <InputGroup>
          <InputGroupAddon addonType="prepend">
            <InputGroupText>#</InputGroupText>
          </InputGroupAddon>
          <Input type="text" innerRef={this.buy} defaultValue = "0"/>
          <InputGroupAddon addonType="append">
            <Button id={buyButton} color="warning" onClick = {this.onBuy}>
              Buy
            </Button>
          </InputGroupAddon>
          <UncontrolledTooltip placement="top" target={buyButton}>
        Funds from your repurchase balance will be spend first.
        </UncontrolledTooltip>
        </InputGroup>}
        </CardBody>
      </Card>
      </div>
    );
  }
}

class RefBox extends Component  {

  constructor(props) {
    super(props);
    this.input = React.createRef();
    this.onCheck = this.onCheck.bind(this);
    this.closeFun = this.closeFun.bind(this);
    this.state = { modal:true };
  }

  onCheck() {
    this.props.checkSponsor(this.input.current.value);
  }

  closeFun() {
    this.setState({ modal:false });
  }

  render() {
    return(
      <Modal isOpen={this.state.modal}>
    <ModalHeader>Your Sponsor</ModalHeader>
    <ModalBody>
      <p dangerouslySetInnerHTML={{__html: sponsorWarning}} />
      <InputGroup>
        <Input valid={this.props.sponsorValid} invalid={this.props.sponsorInvalid} type="text" innerRef={this.input} defaultValue = "" />
        <InputGroupAddon addonType="append">
          <Button color="primary" onClick = {this.onCheck}>
            Check
          </Button>
        </InputGroupAddon>
       </InputGroup>
       </ModalBody>
    <ModalFooter>
      {(this.props.sponsorValid) ? <Button color="success" onClick={this.closeFun}>Ok</Button> : <Button color="danger" onClick={this.closeFun}>I agree to continue without a sponsor</Button>}
    </ModalFooter>
    </Modal>
    );
  }
}

class Doomsday extends Component  {

  constructor(props) {
    super(props);
    this.state = {
      time: this.props.time,
      hours: "00",
      minutes: "00",
      seconds: "00",
    };
  }

  tick() {
    let time = this.state.time
    if (this.state.time < 0) window.location.reload();
    let hours = Math.floor(time / 3600);
    let minutes = Math.floor((time - hours*3600) / 60);
    let seconds = Math.floor(time - (hours*3600 + minutes*60));
    if (hours < 10) hours = "0" + hours;
    if (minutes < 10) minutes = "0" + minutes;
    if (seconds < 10) seconds = "0" + seconds;
    this.setState({time: time - 1, hours: hours, minutes: minutes, seconds: seconds});
  }


  componentDidMount() {
    this.interval = setInterval(() => this.tick(), 1000);
    this.tick()
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return(
      <div className="Doomsday text-center">
      <h1>It's Doomsday</h1>
      <h2>New world starts in:</h2>
      <h1>{this.state.hours}:{this.state.minutes}:{this.state.seconds}</h1>
      </div>
    );
  }
}
export default App;
